.overlay{
    position:absolute;
    top:0;
    width:100%;
    height:100%;
    background-color: rgba(110, 108, 108, 0.582);
    z-index: 2;
    
}

.overlayWindow{
    background-color: whitesmoke;
    width:70%;
    text-align: center;
    margin:auto;
    position: relative;
    min-height: 300px;
    
}

.closeBtn{
    position:absolute;
    top:0;
    right:0;
}